<template>
  <div>
    <div v-if="payStatus">恭喜你支付成功</div>
    <div v-else>支付失败</div>
  </div>
</template>

<script>
import { payMent } from "../api/order.js";
export default {
  name: "paysucess-vue",
  created() {
    this.getData();
  },
  data() {
    return {
      payStatus: false,
    };
  },
  methods: {
    async getData() {
      let data = {
        out_trade_no: this.$router.query.out_trade_no,
        trade_no: this.$router.query.out_trade_no,
      };
      let res = await payMent(data);
      if (res.data.code == 3) {
        this.payStatus = true;
      }
    },
  },
};
</script>

